// src/app/search/page.tsx
"use client";

import { useEffect, useMemo, useState } from "react";
import Link from "next/link";
import sampleData from "@/data/sampleListings";

const SUBTYPES = ["Farmhouse","Villa","Penthouse","Studio","Apartment","Banquet Hall","Rooftop"];
const CATEGORIES = ["Venue","Decorator","Caterer","DJ","Photographer"];

export default function SearchPage() {
  const [category, setCategory] = useState<string>("Venue");
  const [subtype, setSubtype] = useState<string>("");
  const [city, setCity] = useState<string>("");
  const [query, setQuery] = useState("");

  const [startDateStr, setStartDateStr] = useState("");
  const [endDateStr, setEndDateStr] = useState("");
  const [openSubtypeRow, setOpenSubtypeRow] = useState(true);

  // Nights calculation
  const nights = useMemo(() => {
    if (!startDateStr || !endDateStr) return 0;
    const s = new Date(startDateStr);
    const e = new Date(endDateStr);
    const diff = Math.round((e.getTime() - s.getTime()) / (1000*60*60*24));
    return diff > 0 ? diff : 0;
  }, [startDateStr, endDateStr]);

  // Filter listings
  const listings = useMemo(() => {
    return sampleData.filter((l) => {
      if (category && l.category !== category) return false;
      if (subtype && l.subtype !== subtype) return false;
      if (city && !l.city.toLowerCase().includes(city.toLowerCase())) return false;
      if (query && !(
        l.name.toLowerCase().includes(query.toLowerCase()) ||
        l.description.toLowerCase().includes(query.toLowerCase())
      )) return false;
      return true;
    });
  }, [category, subtype, city, query]);

  return (
    <div className="min-h-screen bg-[radial-gradient(circle_at_top_left,#07102a_0%,#03031a_60%)] text-white p-6">
      <div className="max-w-6xl mx-auto">

        {/* TOP FILTER BAR */}
        <div className="mb-6 rounded-md bg-[#07162f]/60 border border-zinc-800 p-4">
          <div className="flex flex-col gap-3 md:flex-row md:items-center">

            {/* Category */}
            <select
              value={category}
              onChange={(e)=>{ setCategory(e.target.value); setSubtype(""); }}
              className="bg-zinc-900 p-2 rounded-md border border-zinc-700"
            >
              {CATEGORIES.map(c => <option key={c} value={c}>{c}</option>)}
            </select>

            {/* City */}
            <input
              placeholder="City (ex: Mumbai)"
              value={city}
              onChange={(e)=>setCity(e.target.value)}
              className="bg-zinc-900 p-2 rounded-md border border-zinc-700 flex-1"
            />

            {/* Date Range */}
            <div className="flex flex-wrap items-center gap-2">
              <label className="text-zinc-400 text-sm">From</label>
              <input
                type="date"
                value={startDateStr}
                onChange={(e)=>setStartDateStr(e.target.value)}
                className="bg-zinc-900 p-2 rounded-md border border-zinc-700"
              />

              <label className="text-zinc-400 text-sm">To</label>
              <input
                type="date"
                value={endDateStr}
                onChange={(e)=>setEndDateStr(e.target.value)}
                className="bg-zinc-900 p-2 rounded-md border border-zinc-700"
              />

              <div className="text-sm text-zinc-300 ml-2">
                {nights ? `${nights} night${nights>1?"s":""}` : "Select dates"}
              </div>
            </div>
          </div>

          {/* SUBTYPE ROW */}
          <div className="mt-4">
            <div className="flex gap-2 items-center mb-2">
              <div className="text-zinc-300 text-sm">Subtype</div>
              <button
                className="text-xs text-zinc-400"
                onClick={()=>setOpenSubtypeRow(s=>!s)}
              >
                {openSubtypeRow ? "Hide" : "Show"}
              </button>
            </div>

            {openSubtypeRow && (
              <div className="flex flex-wrap gap-2">
                <button
                  onClick={()=>setSubtype("")}
                  className={`px-3 py-1 rounded-md ${subtype===""?"bg-blue-600":"bg-zinc-800"}`}
                >
                  All
                </button>

                {SUBTYPES.map(s => (
                  <button
                    key={s}
                    onClick={()=>setSubtype(s)}
                    className={`px-3 py-1 rounded-md ${subtype===s?"bg-emerald-600":"bg-zinc-800"}`}
                  >
                    {s}
                  </button>
                ))}
              </div>
            )}
          </div>

          {/* SEARCH BAR */}
          <div className="mt-4 flex gap-2">
            <input
              value={query}
              onChange={(e)=>setQuery(e.target.value)}
              placeholder="Search venues, decorators, DJs..."
              className="flex-1 bg-zinc-900 p-3 rounded-md border border-zinc-700"
            />
            <button
              onClick={()=>{
                setQuery("");
                setCity("");
                setSubtype("");
                setStartDateStr("");
                setEndDateStr("");
              }}
              className="px-4 py-2 bg-zinc-800 rounded-md border border-zinc-700"
            >
              Reset
            </button>
          </div>
        </div>

        {/* RESULTS COUNT */}
        <div className="mb-4 text-zinc-300">{listings.length} results</div>

        {/* LISTINGS GRID */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {listings.map(l => (
            <div
              key={l.id}
              className="group bg-[#07162f]/60 rounded-xl overflow-hidden border border-zinc-800 hover:scale-[1.01] transition"
            >
              <Link href={`/search/${l.id}`}>
                <div className="relative h-44">
                  <img
                    src={l.images[0]}
                    alt={l.name}
                    className="w-full h-full object-cover brightness-90 group-hover:brightness-110 transition"
                  />
                  <div className="absolute left-3 top-3 bg-black/40 px-2 py-1 rounded text-xs">{l.subtype}</div>
                  <div className="absolute right-3 top-3 bg-black/40 px-2 py-1 rounded text-xs">{l.city}</div>
                </div>

                <div className="p-4">
                  <h3 className="text-lg font-semibold">{l.name}</h3>
                  <p className="text-sm text-zinc-400 mt-2 line-clamp-2">{l.description}</p>

                  <div className="mt-3 flex items-center justify-between">
                    <div className="text-sm text-zinc-300">{l.category} • {l.capacity} pax</div>
                    <div className="text-lg font-bold">₹{l.pricePerNight}</div>
                  </div>
                </div>
              </Link>

              {/* ⭐ LEAVE A REVIEW BUTTON */}
              <div className="p-4 pt-0">
                <Link
                  href={`/search/${l.id}/review`}
                  className="block w-full text-center bg-purple-600 hover:bg-purple-700 rounded-md py-2 mt-2 font-semibold transition"
                >
                  ⭐ Leave a Review
                </Link>
              </div>

            </div>
          ))}
        </div>

      </div>
    </div>
  );
}
